import React, { Component } from "react";

class LSSVideo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div
        className="fb-video"
        data-href={this.props.data.facebook.lastLSS}
        data-show-text="false"
        data-width="600"
        data-allowfullscreen="false"
      >
        <blockquote
          cite={this.props.data.facebook.lastLSS}
          className="fb-xfbml-parse-ignore"
        >
          <a href={this.props.data.facebook.lastLSS}>
            {this.props.data.facebook.lastLSSTitle}
          </a>
          <p>{this.props.data.facebook.lastLSSDesc}</p>
          Posted by <a href={this.props.data.facebook.lirikaURL}>UP LIRIKA</a>
        </blockquote>
      </div>
    );
  }
}

export default LSSVideo;
