import React from "react";
import { CSSTransition } from "react-transition-group";

function CommitteeModal(props) {
  // const showHideClassName = props.show
  //   ? "modal display-block"
  //   : "modal display-none";
  const showHideClassName = props.show
    ? "modal display-block"
    : "modal display-none";

  // const showHideContent = props.show ? "modal-main show" : "modal-main";
  return (
    <div>
      <div className={showHideClassName}>
        <CSSTransition
          in={props.show}
          appear={true}
          timeout={300}
          classNames="modal-fade"
        >
          <div className="modal-main">
            <div className="modal-header">
              <div className="modal-close" onClick={props.handleClose}></div>
            </div>
            <div className="modal-main-grid">
              <div className="modal-img">
                <img src={props.img} alt="person" />
              </div>
              <div className="modal-description">
                <h1>{props.committee}</h1>
                <h3>{`Head: ${props.head}`}</h3>
                <p>{props.desc}</p>
              </div>
            </div>
          </div>
        </CSSTransition>
      </div>
    </div>
  );
}

export default CommitteeModal;
