import React, { Component } from "react";
import img1 from "./../assets/perf-pic-3.jpg";
import img2 from "./../assets/perf-pic-1.jpg";

class Objectives extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgSrc: "",
      mobileImageSrc: "",
    };
  }

  render() {
    return (
      <div className="objectives-section">
        <div className="objectives-grid">
          <div className="objectives-grid-1">
            <img
              src={img1}
              alt="principle"
              className="inline-photo-2 show-on-scroll"
            />
          </div>
          <div className="objectives-grid-2">
            <h1>OUR OBJECTIVES</h1>
            <ul>
              <li>
                <p>{this.props.data.objectives[0]}</p>
              </li>
              <li>
                <p>{this.props.data.objectives[1]}</p>
              </li>
              <li>
                <p>{this.props.data.objectives[2]}</p>
              </li>
              <li>
                <p>{this.props.data.objectives[3]}</p>
              </li>
              <li>
                <p>{this.props.data.objectives[4]}</p>
              </li>
            </ul>
          </div>
          <div className="objectives-grid-3">
            <img
              src={img2}
              alt="mobile"
              className="inline-photo-2 show-on-scroll"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Objectives;
