import React, { Component } from "react";
import SimpleImageSlider from "react-simple-image-slider";

class ImageSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const images = [
      { url: this.props.imgs.src5 },
      { url: this.props.imgs.src6 },
      { url: this.props.imgs.src3 },
      { url: this.props.imgs.src1 },
      { url: this.props.imgs.src8 },
      { url: this.props.imgs.src2 },
      { url: this.props.imgs.src4 },
      { url: this.props.imgs.src10 },
      { url: this.props.imgs.src7 },
    ];
    const style = {
      borderRadius: "0.25em",
      position: "relative",
      // maxWidth: "100%",
      // maxHeight: "100%",
      width: "100%",
      boxShadow: "1em 1em 2em 0.25em rgba(0, 0, 0, 0.2)",
      background: "transparent",
      backgroundPosition: "center",
    };
    return (
      <SimpleImageSlider
        width="100%"
        height="100%"
        images={images}
        showBullets={false}
        style={style}
        navStyle={2}
      />
    );
  }
}

export default ImageSlider;
