import React, { Component } from "react";
import ImageSlider from "./ImageSlider";
import img1 from "./../assets/contact-1.jpg";
import img2 from "./../assets/contact-2.jpg";
import img3 from "./../assets/contact-3.jpg";
import img4 from "./../assets/contact-4.jpg";
import img5 from "./../assets/contact-5.jpg";
import img6 from "./../assets/contact-6.jpg";
import img7 from "./../assets/contact-7.jpg";
import img8 from "./../assets/contact-8.jpg";
import img10 from "./../assets/contact-10.jpg";

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="contact-section">
        <div className="contact-section-header">
          <h1 className="anchor" id="contact">
            Contact Us
          </h1>
          <p>
            <i>Let's make music!</i>
          </p>
        </div>
        <div className="contact-section-body">
          <div className="contact-section-body-text">
            <h3>Partnerships and Sponsorships</h3>
            <p>{this.props.data.contact.email}</p>
            <h3>Phone</h3>
            <p>{this.props.data.contact.phone}</p>
            <h3>Location</h3>
            <p>{this.props.data.contact.location}</p>
            <h3>Social Media Links</h3>
            <div className="contact-socmed">
              <ul>
                <li>
                  <a href={this.props.data.contact.fb}>
                    <i className="fab fa-facebook-f icon"></i>
                  </a>
                </li>
                <li>
                  <a href={this.props.data.contact.twt}>
                    <i className="fab fa-twitter icon"></i>
                  </a>
                </li>
                <li>
                  <a href={this.props.data.contact.sc}>
                    <i className="fab fa-soundcloud icon"></i>
                  </a>
                </li>
                <li>
                  <a href={this.props.data.contact.yt}>
                    <i className="fab fa-youtube icon"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="contact-section-body-image">
            <ImageSlider
              imgs={{
                src1: img1,
                src2: img2,
                src3: img3,
                src4: img4,
                src5: img5,
                src6: img6,
                src7: img7,
                src8: img8,
                src10: img10,
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
