import React from "react";

class About extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="about-section">
        <h1 id="about" className="anchor">
          WHAT IS UP LIRIKA?
        </h1>
        <div className="about-text">
          <p>{this.props.data.about[0]}</p>
          <br />
          <p>{this.props.data.about[1]}</p>
          <br />
          <p>
            <b>{this.props.data.about[2]}</b>
          </p>
        </div>
      </div>
    );
  }
}

export default About;
