import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import $ from "jquery";

ReactDOM.render(<App />, document.getElementById("root"));

//JS Stuff ..

const navSlide = () => {
  const burger = document.querySelector(".burger");
  const nav = document.querySelector(".navlinks");
  const navLinks = document.querySelectorAll(".navlinks li");
  const overlay = document.querySelector(".overlay");
  //Toggle Nav
  burger.addEventListener("click", () => {
    nav.classList.toggle("nav-active");

    //Animate Links
    navLinks.forEach((link, index) => {
      if (link.style.animation) {
        link.style.animation = "";
      } else {
        link.style.animation = `navLinkFade 0.5s ease forwards ${
          index / 7 + 0.3
        }s`;
      }
    });

    //burger animation
    burger.classList.toggle("toggle");

    //dim body background
    overlay.classList.toggle("dim");
  });
};
navSlide();

//navbar logic

$(window).on("scroll", () => {
  if ($(window).scrollTop() || $(window).width() < 768) {
    $("nav").removeClass("clear");
    $("nav").addClass("black");
  } else {
    $("nav").removeClass("black");
    $("nav").addClass("clear");
  }
});

// Detect request animation frame
const scroll =
  window.requestAnimationFrame ||
  // IE Fallback
  function (callback) {
    window.setTimeout(callback, 1000 / 60);
  };
const elementsToShow = document.querySelectorAll(".show-on-scroll");
const contactUsElement = document.querySelector(".image-slider");

function loop() {
  elementsToShow.forEach(function (element) {
    if (isElementInViewport(element)) {
      element.classList.add("is-visible");
    } else {
      element.classList.remove("is-visible");
    }
  });

  if (isElementInViewport(contactUsElement)) {
    contactUsElement.classList.add("is-visible");
  } else {
    contactUsElement.classList.remove("is-visible");
  }

  scroll(loop);
}

// Call the loop for the first time
loop();

// Helper function from: http://stackoverflow.com/a/7557433/274826
function isElementInViewport(el) {
  // special bonus for those using jQuery
  if (typeof $() === "function" && el instanceof $) {
    el = el[0];
  }
  var rect = el.getBoundingClientRect();
  return (
    (rect.top <= 0 && rect.bottom >= 0) ||
    (rect.bottom >=
      (window.innerHeight || document.documentElement.clientHeight) &&
      rect.top <=
        (window.innerHeight || document.documentElement.clientHeight)) ||
    (rect.top >= 0 &&
      rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight))
  );
}
