import React, { Component } from "react";
import img from "./../assets/asset-pic-1.jpg";

class Principles extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgSrc: "",
    };
  }

  // callAPI() {
  //   fetch("http://localhost:9000/assets/asset-pic-1.jpg")
  //     .then((res) => res.blob())
  //     .then((res) => {
  //       this.setState({ imgSrc: URL.createObjectURL(res) });
  //     });
  // }

  // componentDidMount() {
  //   this.callAPI();
  // }
  render() {
    return (
      <div className="principles-section">
        <div className="principles-grid">
          <div className="principles-grid-1">
            <h1>OUR PRINCIPLES</h1>
            <ul>
              <li>
                <p>{this.props.data.principles[0]}</p>
              </li>
              <br />
              <li>
                <p>{this.props.data.principles[1]}</p>
              </li>
            </ul>
          </div>
          <div className="principles-grid-2">
            <img
              src={img}
              alt="principle"
              className="inline-photo-1 show-on-scroll"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Principles;
