import React, { Component } from "react";
import CommitteeModal from "./CommitteeModal";

class PeopleComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
    };
  }

  showModal = () => {
    this.setState({
      showModal: true,
    });
  };
  hideModal = () => {
    this.setState({
      showModal: false,
    });
  };
  render() {
    return (
      <div>
        <div className="people-component">
          <div className="people-component-img"></div>

          <img
            src={this.props.img}
            alt="logo"
            className="show-on-scroll"
            onClick={this.showModal}
          />

          <p className="people-component-name">
            <b>{this.props.data.head}</b>
          </p>
          <p className="people-component-role">
            <i>{this.props.data.title}</i>
          </p>
        </div>
        <CommitteeModal
          show={this.state.showModal}
          handleClose={this.hideModal}
          committee={this.props.data.comm}
          head={this.props.data.head}
          img={this.props.img}
          desc={this.props.data.desc}
        />
      </div>
    );
  }
}

export default PeopleComponent;
