import React, { Component } from "react";
import logoImg from "./../assets/lirika-logo-transparent.png";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = { logoImgSrc: "" };
  }

  // callAPI() {
  //   fetch("http://localhost:9000/assets/lirika-logo-transparent.png")
  //     .then((res) => res.blob())
  //     .then((res) => {
  //       this.setState({ logoImgSrc: URL.createObjectURL(res) });
  //     });
  // }

  // componentDidMount() {
  //   this.callAPI();
  // }

  render() {
    return (
      <div>
        <nav>
          <a href="#top">
            <div className="logo">
              <img src={logoImg} alt="logo" className="navlogo" />
              <h1 className="logo-text">UP LIRIKA</h1>
              <h3 className="logo-text-alt">
                WRITE YOUR STORY. PLAY YOUR MUSIC.
              </h3>
            </div>
          </a>
          <div className="links">
            <ul className="navlinks">
              <li>
                <a href="#about">About</a>
              </li>
              <li>
                <a href="#people">People</a>
              </li>
              <li>
                <a href="#lss">LSS</a>
              </li>
              <li>
                <a href="#contact" className="contact-navlink">
                  Contact
                </a>
              </li>
            </ul>
            <a href="#contact">
              <button>Contact</button>
            </a>
            <div className="burger">
              <div className="line1"></div>
              <div className="line2"></div>
              <div className="line3"></div>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default Navbar;
