import React, { Component } from "react";
import LSSVideoComponent from "./LSSVideo";
class LSS extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="LSS-section">
        <div className="LSS-header">
          <h1 className="anchor LSS-top-anchor" id="lss">
            LIRIKA
          </h1>
          <div className="LSS-header-change"></div>
          <h1 className="anchor LSS-bottom-anchor">SESSIONS</h1>
        </div>
        <div className="LSS-body">
          <div className="LSS-section-vid">
            <LSSVideoComponent data={this.props.data} />
          </div>
          <div className="LSS-section-text">
            <p>{this.props.data.lss.text[0]}</p> <br />
            <p>{this.props.data.lss.text[1]}</p> <br />
            <p>
              <b>{this.props.data.lss.text[2]}</b>
            </p>
            <br />
            <div className="LSS-socmed">
              <p>
                <i>
                  Visit our Facebook to view more LSS productions and check out
                  our SoundCloud to listen to more original music created by our
                  own members!
                </i>
              </p>
              <ul>
                <li>
                  <a href={this.props.data.lss.links.facebook}>
                    <i className="fab fa-facebook-f icon"></i>
                  </a>
                </li>
                <li>
                  <a href={this.props.data.lss.links.soundcloud}>
                    <i className="fab fa-soundcloud icon"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LSS;
